// Inicialização global - roda ANTES do React
// Cria o vídeo UMA VEZ e compartilha entre todos

let globalVideo: HTMLVideoElement | null = null
let globalStream: MediaStream | null = null
let initPromise: Promise<void> | null = null
let orientationPermissionPromise: Promise<boolean> | null = null

export async function initializeGlobal(): Promise<void> {
    if (initPromise) return initPromise

    initPromise = (async () => {
        try {
            console.log('Iniciando inicialização global...')

            // 1. Aguardar A-Frame carregar do HTML
            console.log('Aguardando A-Frame...')
            await waitForAFrame()
            console.log('A-Frame pronto')

            // 2. Inicializar câmera
            console.log('Inicializando câmera...')
            await initializeGlobalCamera()
            console.log('Câmera inicializada')

            // 3. Carregar face-api.js
            console.log('Carregando face-api.js...')
            await loadFaceApi()
            console.log('face-api.js carregado')

            // 4. Inicializar A-Frame Scene ANTES de tudo
            console.log('Inicializando A-Frame Scene...')
            await initializeAFrameScene()
            console.log('A-Frame Scene inicializado')

            console.log('Inicialização global concluída')

            // Configurar listener para quando o modal aparecer
            setupModalListener()
        } catch (error) {
            console.error('Erro na inicialização global:', error)
            throw error
        }
    })()

    return initPromise
}

// Função para aguardar permissão de orientação do dispositivo
export function waitForOrientationPermission(): Promise<boolean> {
    if (orientationPermissionPromise) {
        return orientationPermissionPromise
    }

    orientationPermissionPromise = new Promise((resolve) => {
        let modalVisible = false
        let checkInterval: NodeJS.Timeout | null = null
        let permissionGranted = false
        let modalSeen = false

        // Listener para evento customizado quando o botão é clicado
        const handlePermissionResponse = (event: CustomEvent) => {
            console.log('Botão do modal clicado:', event.detail)

            // Aguardar um pouco e verificar se o modal desapareceu
            setTimeout(() => {
                const modal = document.querySelector('.a-orientation-modal') ||
                    document.querySelector('[class*="orientation-modal"]') ||
                    document.querySelector('[class*="a-orientation"]') ||
                    document.querySelector('[class*="permission"]')

                if (!modal) {
                    console.log('Modal desapareceu após clique - assumindo permissão concedida')
                    permissionGranted = true
                    if (checkInterval) {
                        clearInterval(checkInterval)
                        checkInterval = null
                    }
                    window.removeEventListener('aframe-orientation-permission-response', handlePermissionResponse as EventListener)
                    resolve(true)
                }
            }, 500)
        }

        window.addEventListener('aframe-orientation-permission-response', handlePermissionResponse as EventListener)

        const checkModal = () => {
            const modal = document.querySelector('.a-orientation-modal') ||
                document.querySelector('[class*="orientation-modal"]') ||
                document.querySelector('[class*="a-orientation"]') ||
                document.querySelector('[class*="permission"]') ||
                document.querySelector('[id*="orientation"]') ||
                document.querySelector('[id*="permission"]')

            if (modal && !modalVisible) {
                // Modal apareceu
                modalVisible = true
                modalSeen = true
                console.log('Modal de permissão detectado - aguardando resposta do usuário')
            } else if (!modal && modalVisible && !permissionGranted) {
                // Modal desapareceu - verificar se foi aceito
                modalVisible = false

                console.log('Modal desapareceu - verificando permissão...')

                // Aguardar um pouco e verificar novamente
                setTimeout(() => {
                    const stillNoModal = !document.querySelector('.a-orientation-modal') &&
                        !document.querySelector('[class*="orientation-modal"]') &&
                        !document.querySelector('[class*="a-orientation"]') &&
                        !document.querySelector('[class*="permission"]') &&
                        !document.querySelector('[id*="orientation"]') &&
                        !document.querySelector('[id*="permission"]')

                    if (stillNoModal && modalSeen && !permissionGranted) {
                        console.log('Modal não voltou - permissão provavelmente concedida')
                        permissionGranted = true
                        if (checkInterval) {
                            clearInterval(checkInterval)
                            checkInterval = null
                        }
                        window.removeEventListener('aframe-orientation-permission-response', handlePermissionResponse as EventListener)
                        resolve(true)
                    } else if (!stillNoModal) {
                        // Modal voltou - ainda aguardando
                        console.log('Modal voltou - ainda aguardando')
                        modalVisible = true
                    }
                }, 800)
            } else if (!modal && modalSeen && !permissionGranted) {
                // Modal já foi visto e desapareceu definitivamente
                console.log('Modal já foi visto e desapareceu - verificando permissão final')
                setTimeout(() => {
                    const stillNoModal = !document.querySelector('.a-orientation-modal') &&
                        !document.querySelector('[class*="orientation-modal"]') &&
                        !document.querySelector('[class*="a-orientation"]') &&
                        !document.querySelector('[class*="permission"]')

                    if (stillNoModal && !permissionGranted) {
                        console.log('Permissão concedida - modal definitivamente desapareceu')
                        permissionGranted = true
                        if (checkInterval) {
                            clearInterval(checkInterval)
                            checkInterval = null
                        }
                        window.removeEventListener('aframe-orientation-permission-response', handlePermissionResponse as EventListener)
                        resolve(true)
                    }
                }, 1500)
            }
        }

        // Verificar inicialmente
        checkModal()

        // Verificar a cada 200ms - mais frequente para detectar mais rápido
        checkInterval = setInterval(checkModal, 200)

        // Timeout de segurança - se após muito tempo não resolver, assume que foi concedido se o modal não está mais visível
        setTimeout(() => {
            if (!permissionGranted && modalSeen) {
                const modal = document.querySelector('.a-orientation-modal') ||
                    document.querySelector('[class*="orientation-modal"]') ||
                    document.querySelector('[class*="a-orientation"]') ||
                    document.querySelector('[class*="permission"]')

                if (!modal) {
                    console.log('Timeout: modal não está mais visível, assumindo permissão concedida')
                    permissionGranted = true
                    if (checkInterval) {
                        clearInterval(checkInterval)
                        checkInterval = null
                    }
                    window.removeEventListener('aframe-orientation-permission-response', handlePermissionResponse as EventListener)
                    resolve(true)
                }
            }
        }, 10000) // 10 segundos de timeout
    })

    return orientationPermissionPromise
}

// Função para garantir que o modal seja clicável
function setupModalListener() {
    // Observar quando o modal aparecer
    const observer = new MutationObserver(() => {
        const modal = document.querySelector('.a-orientation-modal') ||
            document.querySelector('[class*="orientation-modal"]') ||
            document.querySelector('[class*="a-orientation"]') ||
            document.querySelector('[class*="permission"]')

        if (modal) {
            console.log('Modal detectado, garantindo que seja clicável...')

                // Garantir que o modal seja clicável
                ; (modal as HTMLElement).style.pointerEvents = 'auto'
                ; (modal as HTMLElement).style.zIndex = '2147483647'
                ; (modal as HTMLElement).style.position = 'fixed'
                ; (modal as HTMLElement).style.display = 'block'
                ; (modal as HTMLElement).style.visibility = 'visible'
                ; (modal as HTMLElement).style.opacity = '1'

            // Garantir que todos os filhos sejam clicáveis
            const allChildren = modal.querySelectorAll('*')
            allChildren.forEach((child) => {
                ; (child as HTMLElement).style.pointerEvents = 'auto'
            })

            // Garantir que botões sejam clicáveis e adicionar listeners
            const buttons = modal.querySelectorAll('button')
            buttons.forEach((button) => {
                button.style.pointerEvents = 'auto'
                button.style.cursor = 'pointer'
                button.style.zIndex = '2147483647'
                button.style.position = 'relative'

                // Remover listeners anteriores se existirem
                const newButton = button.cloneNode(true) as HTMLButtonElement
                button.parentNode?.replaceChild(newButton, button)

                // Adicionar listener para detectar quando o botão é clicado
                const handleClick = (e: Event) => {
                    e.stopPropagation()
                    console.log('Botão do modal clicado:', newButton.textContent || '')
                    // Disparar evento customizado
                    window.dispatchEvent(new CustomEvent('aframe-orientation-permission-response', {
                        detail: { button: newButton.textContent || '', timestamp: Date.now() }
                    }))
                }
                newButton.addEventListener('click', handleClick, { once: true, capture: true })
                newButton.addEventListener('touchstart', handleClick, { once: true, capture: true })
            })

            // Também adicionar listener no modal inteiro para capturar qualquer interação
            const handleModalInteraction = (e: Event) => {
                if (e.target && (e.target as HTMLElement).tagName === 'BUTTON') {
                    console.log('Interação detectada no modal')
                }
            }
            modal.addEventListener('click', handleModalInteraction, { capture: true })
        }
    })

    observer.observe(document.body, {
        childList: true,
        subtree: true,
        attributes: true,
        attributeFilter: ['class', 'style']
    })

    // Verificar imediatamente
    setTimeout(() => {
        const modal = document.querySelector('.a-orientation-modal') ||
            document.querySelector('[class*="orientation-modal"]') ||
            document.querySelector('[class*="a-orientation"]')
        if (modal) {
            observer.disconnect()
            setupModalListener() // Reconfigurar
        }
    }, 1000)
}

function waitForAFrame(): Promise<void> {
    return new Promise((resolve) => {
        if (typeof window !== 'undefined' && (window as any).AFRAME) {
            resolve()
            return
        }

        const checkAFrame = () => {
            if (typeof window !== 'undefined' && (window as any).AFRAME) {
                resolve()
            } else {
                setTimeout(checkAFrame, 100)
            }
        }
        checkAFrame()
    })
}

async function initializeGlobalCamera(): Promise<MediaStream | null> {
    try {
        // Se já existe vídeo e stream, reutilizar
        const existingVideo = document.getElementById('arjs-video') as HTMLVideoElement
        if (existingVideo && existingVideo.srcObject) {
            globalVideo = existingVideo
            globalStream = existingVideo.srcObject as MediaStream
            return globalStream
        }

        // Criar novo stream
        const stream = await navigator.mediaDevices.getUserMedia({
            video: {
                width: { ideal: 1280 },
                height: { ideal: 720 },
                facingMode: { ideal: 'user' }
            },
            audio: false
        })

        // Criar vídeo se não existir
        if (!existingVideo) {
            const video = document.createElement('video')
            video.id = 'arjs-video'
            video.setAttribute('playsinline', '')
            video.setAttribute('autoplay', '')
            video.muted = true
            video.style.position = 'fixed'
            video.style.top = '0'
            video.style.left = '0'
            video.style.width = '100vw'
            video.style.height = '100vh'
            video.style.objectFit = 'cover'
            video.style.zIndex = '0'
            video.style.display = 'none'
            video.style.visibility = 'hidden'
            video.style.opacity = '0'
            document.body.appendChild(video)
            globalVideo = video
        } else {
            globalVideo = existingVideo
        }

        globalVideo.srcObject = stream
        globalStream = stream
        await globalVideo.play()

        return stream
    } catch (err) {
        console.warn('Erro ao inicializar câmera global:', err)
        return null
    }
}

async function loadFaceApi(): Promise<void> {
    if ((window as any).faceapi) {
        return
    }

    return new Promise((resolve, reject) => {
        const script = document.createElement('script')
        script.src = 'https://cdn.jsdelivr.net/npm/face-api.js@0.22.2/dist/face-api.min.js'
        script.onload = () => {
            console.log('face-api.js carregado')
            resolve()
        }
        script.onerror = () => reject(new Error('Failed to load face-api.js'))
        document.head.appendChild(script)
    })
}

async function initializeAFrameScene(): Promise<void> {
    // Criar a-scene ANTES de tudo para inicializar A-Frame
    if (document.querySelector('a-scene')) {
        console.log('A-Frame Scene já existe')
        return
    }

    const scene = document.createElement('a-scene')
    scene.id = 'ar-scene-main'
    scene.setAttribute('embedded', 'true')
    scene.setAttribute('vr-mode-ui', 'enabled: false')
    scene.setAttribute('device-orientation-permission-ui', 'enabled: true')
    scene.setAttribute('renderer', 'alpha: true; antialias: true; colorManagement: true; physicallyCorrectLights: false; sortObjects: true; logarithmicDepthBuffer: false; precision: mediump;')
    scene.setAttribute('webxr', 'enabled: false')
    scene.setAttribute('background', 'transparent: true')
    scene.setAttribute('stats', 'false')

    // Adicionar assets
    const assets = document.createElement('a-assets')
    const skyImg = document.createElement('img')
    skyImg.id = 'skyTexture'
    // Usar caminho relativo - será ajustado pelo Vite
    const base = (import.meta as any)?.env?.BASE_URL || (document?.baseURI ? new URL(document.baseURI).pathname : '/')
    const normalizePath = (path: string) => {
        if (!base || base === '/') return path.startsWith('/') ? path : `/${path}`
        const b = base.endsWith('/') ? base : base + '/'
        return path.startsWith('/') ? b + path.slice(1) : b + path
    }
    skyImg.src = normalizePath('assets/images/SKY.jpg')
    skyImg.setAttribute('crossorigin', 'anonymous')
    assets.appendChild(skyImg)
    scene.appendChild(assets)

    // Adicionar skybox
    const sky = document.createElement('a-sky')
    sky.setAttribute('rotation', '0 -90 0')
    sky.setAttribute('material', 'src: #skyTexture; opacity: 0.5; transparent: true')
    scene.appendChild(sky)

    // Adicionar câmera
    const camera = document.createElement('a-entity')
    camera.id = 'camera'
    camera.setAttribute('camera', 'active: true')
    camera.setAttribute('look-controls', 'enabled: true; reverseMouseDrag: false; touchEnabled: true; magicWindowTrackingEnabled: true; pointerLockEnabled: false; touchSensitivity: 1; mouseSensitivity: 1')
    camera.setAttribute('wasd-controls', 'enabled: false')
    camera.setAttribute('position', '0 1.6 0')
    scene.appendChild(camera)

    // Adicionar luzes
    const ambientLight = document.createElement('a-entity')
    ambientLight.setAttribute('light', 'type: ambient; color: #888')
    scene.appendChild(ambientLight)

    const directionalLight = document.createElement('a-entity')
    directionalLight.setAttribute('light', 'type: directional; color: #fff; intensity: 0.6')
    directionalLight.setAttribute('position', '1 1 1')
    scene.appendChild(directionalLight)

    // Adicionar head-cube (seguir rotação da cabeça)
    const headCube = document.createElement('a-entity')
    headCube.id = 'head-cube'
    headCube.setAttribute('geometry', 'primitive: box; width: 0.3; height: 0.3; depth: 0.3')
    headCube.setAttribute('material', 'color: #FF6B6B; opacity: 0; transparent: true')
    headCube.setAttribute('position', '0 1 -1.2')
    headCube.setAttribute('rotation', '0 0 0')
    scene.appendChild(headCube)

    // Adicionar ao body (escondido inicialmente)
    scene.style.position = 'fixed'
    scene.style.top = '0'
    scene.style.left = '0'
    scene.style.width = '100%'
    scene.style.height = '100%'
    scene.style.zIndex = '-100' // A-Frame abaixo de tudo
    scene.style.background = 'transparent'
    document.body.appendChild(scene)

    // Aguardar A-Frame inicializar completamente
    await new Promise<void>((resolve) => {
        const checkReady = () => {
            const sceneEl = document.querySelector('a-scene') as any
            if (sceneEl && sceneEl.hasLoaded) {
                resolve()
            } else {
                setTimeout(checkReady, 100)
            }
        }
        checkReady()
    })

    console.log('A-Frame Scene criado e inicializado')
}

export function getGlobalVideo(): HTMLVideoElement | null {
    return globalVideo || (document.getElementById('arjs-video') as HTMLVideoElement)
}

export function getGlobalStream(): MediaStream | null {
    return globalStream || (getGlobalVideo()?.srcObject as MediaStream) || null
}

